body {
  transition: 0.3s;
  position: relative;
}
body.no-scroll {
  overflow: hidden;
}
body.opacity-layer {
  position: relative;
  transition: 0.3s;
}
body.opacity-layer:after {
  content: '';
  background-color: #6fbcbc;
  opacity: 0.3;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 2;
