.header {
  background-color: #fff;
}
.header__top {
  padding: 27px 0 22px;
}
.header__bottom {
  border-top: 1px solid #e9ebf0;
  padding: 19px 0;
}
.header__wrap {
  display: flex;
  align-items: center;
  position: relative;
}
.header .logo svg {
  width: 185px;
  height: 53px;
}
.header .sites {
  margin-left: auto;
  position: relative;
}
.header .sites__toggle {
  color: #1778bb;
  font-size: 13px;
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}
.header .sites__toggle:before {
  content: '';
  position: absolute;
  left: 0;
  top: -4px;
  width: 20px;
  height: 20px;
  background-image: url('../img/icons/sites-arrow.png');
  background-size: 8px 5px;
  background-position: center;
  background-repeat: no-repeat;
  border: 2px solid #1778bb;
  border-radius: 100px;
  transition: 0.3s;
}
.header .sites__toggle.active:before {
  transform: rotate(180deg);
  transition: 0.3s;
}
.header .sites__content {
  display: none;
  position: absolute;
  top: calc(100% + 10px);
  left: 0;
  width: 100%;
  background-color: #1778bb;
  padding: 20px;
  z-index: 3;
}
.header .sites__item {
  color: #fff;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  display: block;
}
.header .sites__item:not(:last-of-type) {
  margin-bottom: 20px;
}
.header .btn {
  background-color: #1778bb;
  color: #fff;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  padding: 9px 14px;
  border-radius: 100px;
  margin-left: 42px;
}
.header .phone {
  color: #1074b9;
  text-align: left;
  margin-left: 32px;
}
.header .phone .time {
  font-size: 12px;
  font-weight: 400;
  display: block;
}
.header .phone .link {
  font-weight: 700;
  font-size: 17px;
}
.header .nav {
  padding: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.header .nav__item {
  color: #243970;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 0 8px;
}
.header .nav__item.green {
  color: #1db479;
}
.header .burger {
  position: relative;
  display: none;
  right: 0;
  top: 0;
  width: 30px;
  height: 21px;
  z-index: 4;
  border-radius: 100px;
}
.header .burger:before {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 0 #5070bd;
  transition: 1s;
  width: 2px;
  height: 2px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  border-radius: 100px;
  display: block;
}
.header .burger.active:before {
  box-shadow: 0 0 0 5000px #5070bd;
}
.header .burger.active span {
  background-color: #fff;
}
.header .burger.active span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 9px;
}
.header .burger.active span:nth-of-type(2) {
  opacity: 0;
}
.header .burger.active span:nth-of-type(3) {
  transform: rotate(-45deg);
  bottom: 9px;
}
.header .burger span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 3px;
  background-color: #5070bd;
  display: block;
  border-radius: 100px;
  transition: 0.5s;
  transform: rotate(0);
  opacity: 1;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
.header .mobile-menu {
  position: fixed;
  top: 50px;
  right: 0;
  height: calc(100vh - 50px);
  overflow: auto;
  width: 100%;
  z-index: 4;
  padding: 0 40px 40px;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-20px);
  transition: 0.5s;
  transition-delay: 0.2s;
}
.header .mobile-menu.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.header .mobile-menu .nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}
.header .mobile-menu .nav__item {
  border: none;
  color: #fff;
  padding: 8px 0;
  font-size: 17px;
  font-weight: 600;
}
.header .mobile-menu .nav__item.green {
  color: #1db479;
}
.header .mobile-menu .nav__item:not(:first-of-type) {
  margin-left: 0;
}
.header .mobile-menu .sites {
  margin-top: 20px;
  display: block;
}
.header .mobile-menu .sites__toggle {
  color: #fff;
  font-size: 13px;
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}
.header .mobile-menu .sites__toggle:before {
  content: '';
  position: absolute;
  left: 0;
  top: -4px;
  width: 20px;
  height: 20px;
  background-image: url('../img/icons/white-arrow.png');
  background-size: 8px 5px;
  background-position: center;
  background-repeat: no-repeat;
  border: 2px solid #fff;
  border-radius: 100px;
}
.header .mobile-menu .sites__content {
  display: none;
  position: static;
  background: none;
  padding: 20px 30px;
}
.header .mobile-menu .sites__item {
  color: #fff;
}
.header .mobile-menu .sites__item:not(:last-of-type) {
  margin-bottom: 10px;
}
.header .mobile-menu .btn {
  margin-left: 0;
  margin-top: 20px;
  display: inline-block;
  background-color: #fff;
  color: #243970;
}
.header .mobile-menu .phone {
  display: block;
  color: #fff;
  margin-top: 20px;
  margin-left: 0;
  text-align: left;
}
@media screen and (max-width: 1200px) {
  .header__top {
    padding: 14px 0;
  }
  .header .logo svg {
    width: 120px;
    height: 34px;
  }
  .header .nav__item {
    font-size: 12px;
    padding: 0 7px;
  }
  .header .btn {
